
/* ----------------- SITES ----------------- */
.sites {
  margin-top: 60px;
  &__wrap {
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 50px;
    position: relative;

    @include width-1700 {
      gap: 100px;
    }
    @include width-1450 {
      gap: 60px;
    }
    @include width-1250 {
      grid-template-columns: 1fr;
      gap: 40px;
    }
  }

  &__body {
  }
  &__img {
    width: 100%;
    max-width: 496px;
    height: fit-content;
    margin: 0 auto;
    padding: 0 10px;
    position: sticky;
    top: 0;
  }
  &__items {
    margin-top: 60px;

    @include width-1250 {
      margin-top: 80px;
    }
  }
}
.sites-item {
  width: 100%;
  padding: 40px 0;
  padding-right: 35px;
  display: flex;
  justify-content: space-between;
  position: relative;
  border-top: 2px solid #000000;
  &:last-child {
    border-bottom: 2px solid #000000;
  }
  &:hover {
    & .sites-item__hover {
      clip-path: inset(0 0 0 0);
    }
  }

  @include width-1700 {
    padding: 20px 0;
    padding-right: 35px;
  }
  @include width-640 {
    flex-direction: column;
    padding-top: 30px;
  }

  &__left {
    position: relative;
    z-index: 1;
    pointer-events: none;

    @include width-1450 {
      max-width: 340px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 30px;
    line-height: 36px;
    color: #000000;
    margin-bottom: 10px;
    max-width: 500px;

    @include width-1700 {
      max-width: 400px;
    }
    @include width-1450 {
      font-size: 26px;
      line-height: 31px;
    }
    @include width-640 {
      @include scale(font-size,26,20);
      line-height: 120%;
    }
  }

  &__subtitle {
    display: block;
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    color: #000000;
    max-width: 450px;

    @include width-640 {
      @include scale(font-size,18,16);
    }
  }
  &__more {
    position: relative;
    z-index: 1;
    pointer-events: none;

    @include width-640 {
      margin-top: 40px;
      margin-bottom: 30px;
    }
  }
  &__hover {
    display: block;
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    max-width: 360px;
    height: 100%;
    clip-path: inset(100% 0 0 0);
    background: $yellow-dark;
    transition: 0.3s linear;
    cursor: pointer;

    @include width-1700 {
      max-width: 335px;
    }
    @include width-1450 {
      max-width: 285px;
    }
    @include width-768 {
      max-width: 240px;
      clip-path: inset(0 0 0 0);
    }
    @include width-640 {
      max-width: 100%;
      height: 60px;
      top: auto;
      bottom: 30px;
    }
  }
}
/* ----------------- /SITES ----------------- */
