.footer {
  background: $yellow-dark;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1;
  bottom: 0;
  left: 0;
  overflow: hidden;
  padding-top: 100px;
  padding-bottom: 20px;
  display: flex;
  align-items: flex-end;
  &__row {
    display: grid;
    grid-template-columns: 390px 1fr;
    gap: 20px;
    align-items: center;
    &:not(:last-child) {
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    }
    &:nth-last-child(-n+2) {
      border-bottom: 0;
    }
    &:nth-child(1) {
      padding-bottom: 70px;
      @include width-1250 {
        padding-bottom: 50px;
      }
    }
    &:nth-child(2) {
      padding: 20px 0;
    }
    &:nth-child(3) {
      padding-top: 30px;
      @include width-960 {
        padding-top: 60px;
      }
    }
    &:last-child {
      margin-top: 60px;
      gap: 20px;
    }

    @include width-1700 {
      grid-template-columns: 335px 1fr;
    }
    @include width-1450 {
      grid-template-columns: 285px 1fr;
    }
    @include width-1250 {
      grid-template-columns: 240px 1fr;
    }
    @include width-960 {
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  &__arrow {
    stroke: #000000;
    stroke-width: 4px;
    width: 112px;
    height: fit-content;

    @include width-1250 {
      width: 90px;
    }
    @include width-960 {
      width: 57px;
      margin-bottom: 20px;
    }
  }

  &__btn {
    display: block;
    background: none;
    border: none;
    padding: 0;
    font-weight: 500;
    font-size: 120px;
    line-height: 100%;
    color: #000000;
    position: relative;
    width: fit-content;
    cursor: pointer;
    &::after {
      content: '';
      position: absolute;
      bottom: -15px;
      left: 0;
      width: 100%;
      height: 10px;
      background: #000000;
      transition: 0.3s;
    }
    &:hover {
      &::after {
        opacity: 0;
        visibility: hidden;
        transition: 0.3s;
      }
    }

    @include width-1450 {
      font-size: 108px;
    }
    @include width-1250 {
      font-size: 80px;
      &::after {
        height: 7px;
      }
    }
    @include width-960 {
      font-size: 70px;
      &::after {
        height: 5px;
        bottom: -10px;
      }
    }
    @include width-640 {
      @include scale(font-size,70,36);
      &::after {
        @include scale(height,5,3);
      }
    }
  }

  &__logo {
    width: fit-content;
    height: fit-content;
    margin: 0;
    & span {
      color: $yellow-dark;
    }
    @include width-640 {
      display: none;
    }
  }

  &__contacts {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include width-1250 {
      flex-direction: column;
      align-items: flex-start;
    }
    @include width-960 {
      margin-top: 10px;
    }
  }

  &__contacts-email {
    font-weight: 500;
    font-size: 60px;
    line-height: 150%;
    color: #000000;
    margin-right: 50px;
    width: fit-content;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 5px;
      background: #000000;
      opacity: 0;
      visibility: hidden;
      transition: 0.3s;
    }
    &:hover {
      &::after {
        opacity: 1;
        visibility: visible;
        transition: 0.3s;
      }
    }

    @include width-1250 {
      font-size: 50px;
    }
    @include width-960 {
      font-size: 50px;
    }
    @include width-640 {
      @include scale(font-size,50,30)
    }
  }

  &__contacts-social {
    & a {
      font-weight: 500;
      font-size: 25px;
      line-height: 150%;
      color: #000000;
      transition: 0.3s;
      width: fit-content;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 100%;
        height: 2px;
        background: #000000;
        opacity: 0;
        visibility: hidden;
        transition: 0.3s;
      }
      &:hover {
        &::after {
          opacity: 1;
          visibility: visible;
          transition: 0.3s;
        }
      }
      &:last-child {
        margin-left: 30px;
      }

      @include width-1450 {
        font-size: 16px;
      }
    }
  }

  &__nav {
    & ul {
      display: flex;
      & li {
        &:not(:last-child) {
          margin-right: 20px;
          @include width-640 {
            margin-right: 10px;
          }
        }
        & a {
          font-weight: 500;
          font-size: 20px;
          line-height: 150%;
          color: #000000;
          width: fit-content;
          position: relative;
          &::after {
            content: '';
            position: absolute;
            left: 0;
            bottom: -5px;
            width: 100%;
            height: 2px;
            background: #000000;
            opacity: 0;
            visibility: hidden;
            transition: 0.3s;
          }
          &:hover {
            &::after {
              opacity: 1;
              visibility: visible;
              transition: 0.3s;
            }
          }

          @include width-640 {
            @include scale(font-size,20,14)
          }
        }
      }
    }
  }

  &__rights {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: rgba(0, 0, 0, 0.4);
  }
}
