.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #ffffff;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  &.active {
    opacity: 1;
    visibility: visible;
    transition: 0.3s;
  }
  &-close {
    position: absolute;
    right: 50px;
    top: 40px;
    & img {
      width: 60px;
      height: 60px;
    }

    @include width-768 {
      right: 10px;
      top: 10px;
    }
    @include width-640 {
      & img {
        width: 44px;
        height: 44px;
      }
    }
  }
  &-body {
    padding: 100px 0;
  }
}

.popup-clients {
  overflow-y: scroll;
  &__cards {
    margin-top: 40px;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-60%);
    transition: 1s;
    &.active {
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
      transition: 1s;
    }
  }
}
